           this.data = res.data.data
           this.show_data = this.data.
          //  console.log(this.show_data)
       }).catch(res => {
         console.log(res);
       })
    },


    //  http://localhost:8080/shopping/content/{goodsId}
    openInfo (id) {
      // console.log(id)
      this.$http({
        url: '/shopping/content/'+id,
        method: 'get'
      }).then(res => {
          // console.log(res)
          this.targetData = res.data.data
          // console.log(this.targetData)
      }).catch(res => {
        console.log(res);
      })

      this.dialogVisible = true
    },


    // 
    handleClose(done) {
      done();
    },


    //  
    changeShow (type_str) {
      //       
      if ("" == type_str) {
        this.show_data = this.data.
      }
      else{
        var new_show_data = [];
        for (var i = 0; i < this.data..length; i++) {
          var item = this.data.[i];
          if (item.goodsTag == type_str) {
            new_show_data.push(item)
          }
        }
        this.show_data = new_show_data;
      }
    },

    //  http://localhost:8080/shopping/search
    children_search_note (search_text) {
      // console.log('')
      // console.log(search_text)
      this.$http({
        url: '/shopping/search',
        method: 'get',
        params: {
          param: search_text
        }
      }).then(res => {
        this.show_data = res.data.data
      }).catch(res => {
        console.log(res);
      })
    },
    
    // 
    handleChange(value) {
        console.log(value);
    },

    //  http://localhost:3000/shoppingCart/add/{goodsId}
    addShoppingCart () {
      this.$http({
        url: '/shoppingCart/add/' + this.targetData.goods.id,
        method: 'post',
        params: {
          count: this.goodsCount
        }
      }).then(res => {
        this.$message.success(res.data.data)
      }).catch(res => {
        console.log(res);
      })
    }

  }
}
