<template>
  <div class="ShopCar">
    <!--  -->
    <div>
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/Red/Shop/Goods' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>

    </div>
    .
    <br>

    <div style="position:relative; height: 30px;line-height:30px;">

      <!-- <div style="position:absolute;left:10px;top: 50%; transform: translateY(-50%);">
        <el-tag type="warning">
           {{ userInformation.username}}
        </el-tag>
        &nbsp;&nbsp;
        <el-tag>
           {{ userInformation.wallet}} 
        </el-tag>
      </div> -->

      <div style="position:absolute;right:10px;top: 50%; transform: translateY(-50%);">
        <!-- <el-button type="success" plain @click="warnShow()"></el-button> -->
        <el-button type="primary" plain  @click="totalBuy()"></el-button>
      </div>

    </div>

    <br>
    <el-table :data="shop_cart.slice((currentPage - 1) * pageSize, currentPage * pageSize)" 
    stripe style="width: 100%; text-align: center;" 
    @selection-change="handleSelectionChange" 
    >

      <!--  -->
      <el-table-column type="selection" width="55">
      </el-table-column>

      <el-table-column prop="picture" label="" width="180">
        <template v-slot="scope">
          <img :src="scope.row.picture" alt="" style="width:80px;height: 80px; border-radius: 8px;">
        </template>
      </el-table-column>

      <el-table-column prop="goodsName" label="" width="180">
      </el-table-column>


      <el-table-column prop="price" label="">
      </el-table-column>


      <el-table-column  prop="count" label="">
        <template v-slot="scope">
          <el-input-number 
            v-model="shop_cart[scope.$index].count" :min="1" :max="10" label=""  
            @change="(currentValue, oldValue) => handleChange(currentValue, oldValue, scope.row)">
          </el-input-number>
        </template>
      </el-table-column>


      <el-table-column prop="amount" label="">
        <template v-slot="scope">
          <el-tag>{{ shop_cart[scope.$index].amount }}</el-tag>
        </template>
      </el-table-column>


      <el-table-column prop="shopping_cart_id" label="">
        <template v-slot="scope">
          <el-button type="danger" plain @click="removeShopCartByIndex(scope.$index)"></el-button>
          <!-- <el-button type="primary" plain @click="simpleBuy(scope.$index)"></el-button> -->
        </template>
      </el-table-column>

    </el-table>
    
    <!--  -->
    <div style="background-color:white; width: 100%; position: fixed; top: 90%">
      <div class="block" >
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage"
          :page-sizes="[2, 4, 6, 8, 10]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
          :total="shop_cart.length">
        </el-pagination>
      </div>
    </div>
  </div>
</template>
  
<script>


export default {
  data() {
    return {
      // 
      shop_cart: [
        {
            "created": "2023-04-29T14:47:35.000+00:00",
            "updated": "2023-04-29T16:36:10.000+00:00",
            "id": 8,
            "userId": 1,
            "goodsId": 1,
            "goodsName": "",
            "price": 18.5,
            "picture": "https://tanhua-zxm.oss-cn-hangzhou.aliyuncs.com/%E5%B0%8F%E7%BA%A2%E4%B9%A6/%E5%95%86%E5%93%81/%E6%8E%A8%E8%8D%90/1/O1CN0160sQnm23r793wD3S8_%21%21267817308.jpg",
            "count": 1,
            "amount": 18.5,
            "deleted": 0
        }
      ],
      // 
      userInformation: {
        "grade": "vip",
        "level": 1,
        "password": "123456",
        "state_message": "",
        "type": "root",
        "user_id": 4,
        "user_picture": "http://192.168.123.53:8888/sports_shop_backend_war/users_picture/kzb68up.jpg",
        "username": "",
        "wallet": -2995
      },
      // 
      currentPage: 1,
      // :page-sizehandleSizeChange()
      pageSize: 4,
  
      // 
      multipleSelection: [],
      }
  },
  mounted () {
    this.getShopCart();
    // this.getUserInfo();
  },
  methods: {

    //  http://localhost:3000/shoppingCart tokenuserID
    getShopCart () {
      this.$http({
        url: '/shoppingCart',
        method: 'get'
      }).then(res => {
        this.shop_cart = res.data.data
      }).catch(res => {
        console.log(res);
      })
    },

    //    shopping_cart_id 
    // http://localhost:8080/shoppingCart/remove/{goodsId}
    removeShopCartByIndex (index) {
      // console.log(this.shop_cart[index].shopping_cart_id)
      this.$http({
        url: '/shoppingCart/remove/' + this.shop_cart[index].id,
        method: 'put'
      }).then(res => {
        this.$message.error(res.data.data)
      }).catch(res => {
        console.log(res);
      })
    },

    // 
    handleChange(currentValue, oldValue,rowData) {
      //  console.log(currentValue) // 
      //  console.log(oldValue) // 
      //  console.log(rowData) //  idrowData.id

      //  http://localhost:8080/shoppingCart/addCount/{goodsId}
      if (currentValue - oldValue > 0) {
          this.$http({
            url: '/shoppingCart/addCount/'+rowData.id,
            method: 'post'
          }).then(res => {
            this.$message.success(res.data.data)
          }).catch(res => {
            console.log(res);
          })
      }
      //  http://localhost:8080/shoppingCart/reduceCount/{goodsId}
      else if (currentValue - oldValue < 0) {
          this.$http({
              url: '/shoppingCart/reduceCount/'+rowData.id,
              method: 'post'
            }).then(res => {
              this.$message.success(res.data.data)
            }).catch(res => {
              console.log(res);
            })
      }
      // 
      this.getShopCart()
    },

    // 
    handleSizeChange(val) {
      // console.log(` ${val} `);
      this.pageSize = val;
    },

    // 
    handleCurrentChange(val) {
      // console.log(`: ${val}`);
      this.currentPage = val;
    },

    // 
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },

    // 
    // packageDelete() {
    //   for (var i = 0; i < this.multipleSelection.length; i++) {
    //     // this.removeOrderByOrderId(this.multipleSelection[i].order_id)
    //     this.deleteOrdersById (this.multipleSelection[i].order_id)
    //   }
    // },

    //  http://localhost:8080/shoppingCart/settlement
    totalBuy () {
      const mylist = []
      for (var i = 0; i < this.multipleSelection.length; i++) {
        mylist.push(this.multipleSelection[i].id)
      }
      console.log(mylist)
      this.$http({
        url: '/shoppingCart/settlement',
        params: {
          goodsIdList: mylist
        },
        method: 'post'
      }).then(res => {
        this.$message.success(res.data.data)
      }).catch(err => {
        console.log(err);
      })
    },
  }
}
</script>

<style>
.ShopCar {
  width: 100%;
  height: 100%;
  overflow: scroll;
  position: relative;
  overflow: scroll;
}
.table_scrollbar {
  margin-bottom: 150px;
}
.ShopCar::-webkit-scrollbar {
    width: 0;
    height: 0;
    background-color: transparent;
}
</style>