<script>

export default {
  data() {
    return {
      username: window.sessionStorage.getItem("username"),
      // 
      orders: [
        {
          "created": "2023-04-30T06:22:48.000+00:00",
          "updated": null,
          "id": 10,
          "goodsId": 1,
          "userId": 1,
          "goodsName": "",
          "picture": "https://tanhua-zxm.oss-cn-hangzhou.aliyuncs.com/%E5%B0%8F%E7%BA%A2%E4%B9%A6/%E5%95%86%E5%93%81/%E6%8E%A8%E8%8D%90/1/O1CN0160sQnm23r793wD3S8_%21%21267817308.jpg",
          "count": 1,
          "amount": 18.5,
          "deleted": 0
        }
      ],

      // 
      currentPage: 1,
      // :page-sizehandleSizeChange()
      pageSize: 4,

      // 
      multipleSelection: [],

      //   
      centerDialogVisible: false,

      // 
      params: {
        id: 0,
        comment: ""
      }
    }
  },
  mounted() {
    this.getOrders()
  },
  methods: {
    //  http://localhost:8080/order
    getOrders() {
      this.$http({
        url: '/order',
        method: 'get'
      }).then(res => {
        this.orders = res.data.data;
      }).catch(res => {
        console.log(res);
      })
    },

    // 
    handleSizeChange(val) {
      // console.log(` ${val} `);
      this.pageSize = val;
    },

    // 
    handleCurrentChange(val) {
      // console.log(`: ${val}`);
      this.currentPage = val;
    },

    // 
    handleSelectionChange(val) {
      this.multipleSelection = val;
      // console.log(val)
    },

    //  
    readyForComment(index) {
      // 1id
     this.params.order_id = this.orders[index].id;
     // 2
     this.centerDialogVisible = true
    },

    //  http://localhost:8080/order/comment/{goodsId}
    addComment() {
      var _this = this;
      this.$http({
        url: '/order/comment/'+_this.params.id,
        params: {
          comment: _this.params.comment
        },
        method: 'post'
      }).then(res => {
        this.$message.error(res.data.data)
        _this.centerDialogVisible = false
      }).catch(res => {
        console.log(res);
      })
    },
  }
}
</script>

