import Vue from 'vue'
import App from './App.vue'
// 
import './assets/style/theme/index.css'
// css
import './assets/global.css'
// 
import router from './router'
// elementUI
import ElementUI from 'element-ui';
// import 'element-ui/lib/theme-chalk/index.css';
// echarts
import * as echarts from 'echarts'
// axios
import  axios from 'axios'
// axios.defaults.baseURL = 'http://192.168.123.53:8080/' // -
// axios.defaults.baseURL = 'http://localhost:8080/' // -
// axios.defaults.baseURL = 'http://10.22.235.176:8080/' // -
// axios.defaults.baseURL = 'http://192.168.123.139:8080/' // -
// axios.defaults.baseURL = 'http://10.22.234.77:8080' // -
axios.defaults.baseURL = 'http://localhost:3000/' // nodeJS Express.js  

// videoJSOSSM3U8
import videoJS from 'video.js'
import 'video.js/dist/video-js.css'
import 'videojs-flash'; // rtmp, 


axios.interceptors.request.use(config => {
  // tokenAuthorization
  config.headers.Authorization = window.sessionStorage.getItem('token')
  return config
})


Vue.prototype.$echarts = echarts;
Vue.prototype.$http = axios;
Vue.prototype.$video = videoJS

Vue.use(ElementUI);

Vue.config.productionTip = false

// -
Vue.filter('dateFormat', function (originVal) {
  const dt = new Date(originVal)

  const y = dt.getFullYear()
  const m = (dt.getMonth() + 1 + '').padStart(2, '0')
  const d = (dt.getDate() + '').padStart(2, '0')

  const hh = (dt.getHours() + '').padStart(2, '0')
  const mm = (dt.getMinutes() + '').padStart(2, '0')
  const ss = (dt.getSeconds() + '').padStart(2, '0')
  // `yyyy-mm-dd hh:mm:ss`
  return `${y}-${m}-${d} ${hh}:${mm}:${ss}`
})

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
