          status = res.data.data
          console.log(status)
          if (status == '') {
            this.$message.error('');
          }
          else {
            // 
            if ("el-icon-success" == this.zan_class) {
              this.zan_class = "el-icon-circle-check"
              this.targetData.praisenum = this.targetData.praisenum -1;
              
            }
            // 
            else {
              this.zan_class = "el-icon-success"
              this.targetData.praisenum = this.targetData.praisenum +1;
            }
          }
      }).catch(res => {
        console.log(res);
      })
    },

