<template>

    <div class="login">
        <div class="asidePic"></div>
        <div class="fuc">


            <!-- <div class="pic">
                <span></span>
            </div> -->


            <div class="inp">

                <div>

                    <div style="text-align: center; color:#F2F6FC;">
                        <div style="height:86px;width: 86px; line-height: 86px; position: absolute; left: 0;">
                            <span></span>
                        </div>
                        <el-upload class="avatar-uploader" action="" :http-request="upload" :show-file-list="false"
                            :on-success="handleAvatarSuccess" :before-upload="beforeAvatarUpload">
                            <img v-if="headphoto" :src="headphoto" class="avatar">
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                    </div>

                    <div style="margin-top:10px">
                        <el-input placeholder="" prefix-icon="el-icon-user-solid" v-model="username">
                        </el-input>
                    </div>

                    <div style="margin-top:10px">
                        <el-input placeholder="" prefix-icon="el-icon-edit" show-password
                            v-model="password"></el-input>
                    </div>

                    <div style="margin-top:10px">
                        <el-radio v-model="gender" label="1" border>  <i class="el-icon-male"></i></el-radio>
                        <el-radio v-model="gender" label="0" border> <i class="el-icon-female"></i></el-radio>
                    </div>
                    
                    <div style="margin-top:10px">
                      <el-input placeholder="" prefix-icon="el-icon-phone-outline"  v-model="phone"></el-input>
                    </div>

                    <div style="margin-top:10px">
                      <el-input placeholder="" prefix-icon="el-icon-time"  v-model="age"></el-input>
                    </div>

                    <div style="margin-top:10px;display:flex;justify-content:center;">
                        <el-button type="primary" plain @click="CreateUser"></el-button>
                        <el-button type="success" plain @click="gotoLogin"></el-button>
                        <!-- <el-button type="warning" plain>Reset</el-button> -->
                    </div>

                </div>
            </div>

        </div>
    </div>

</template>
<script>
export default {
    data() {
        return {
            headphoto: '',
            username: '',
            password: '',
            gender: 1,
            age: 18,
            introduction: '',
            phone: 19813430639
        }
    },
    methods: {
        // 
        CreateUser() {
            this.$http({
                url: '/user/register',
                data: {
                    //
                    headphoto: this.headphoto,
                    username: this.username,
                    password: this.password,
                    gender: this.gender,
                    age: this.age,
                    introduction: this.introduction,
                    phone: this.phone
                },
                method: 'post'
            }).then(res => {
                // token
                if (res.status == 200 && res.data.data == "") {
                    this.$message.success('')
                    this.$router.push("/login");
                } else {
                    return this.$message.error('')
                }
                // console.log(res)
            }).catch(res => {
                console.log(res);
            })
        },

        // 
        upload(param) {
            const formdate = new FormData()
            formdate.append('headphoto', param.file)
            const url = '/user/headphoto'
            const headers = {
                'Content-Type': 'multipart/form-data'
            }
            // axios.post
            this.$http.post(
                url,
                formdate,
                headers
            ).then(res => {
                // console.log(data)
                this.$message.success("")
                this.headphoto = res.data.data
            }).catch(response => {
                console.log(response)
            })
        },
        gotoLogin() {
            this.$router.push("/login/coreLogin");
        },
        handleAvatarSuccess(res, file) {
            this.headphoto = URL.createObjectURL(file.raw);
        },
        beforeAvatarUpload(file) {
            const isJPG = file.type === 'image/jpeg';
            const isLt2M = file.size / 1024 / 1024 < 2;

            if (!isJPG) {
                this.$message.error(' JPG !');
            }
            if (!isLt2M) {
                this.$message.error(' 2MB!');
            }
            return isJPG && isLt2M;
        }
    }
}
</script>
