<template>
  <div>
    <div class="pubnote">
      <el-menu :default-active="pic_or_vlog"  mode="horizontal"  @select="handleSelect">
        <el-menu-item index="pub_picture"></el-menu-item>
        <el-menu-item index="pub_vlog"></el-menu-item>
      </el-menu>

      <!--  -->
      <div class="note" v-if="pic_or_vlog == 'pub_picture' ">
         <div class="note_real">

            <div class="cencen">
              <span></span>
              <el-button type="text"> &nbsp; +</el-button> &nbsp;&nbsp; <em style="font-size: small; font-weight: 100;"></em>
              <br>
              <el-upload class="avatar-uploader" action="" :http-request="upload_PictureNode_surfacePicture" :show-file-list="false"
                  :on-success="handleAvatarSuccess_pictureNode" :before-upload="beforeAvatarUpload_pictureNode">
                  <img v-if="pictureNode.surfacePicture" :src="pictureNode.surfacePicture" class="avatar">
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </div>

            <div class="cencen">
              <span></span> <br> <br>
              <el-upload
                class="upload-demo"
                action=""
                :http-request="upload_PictureNode_pictureurl"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                :file-list="fileList"
                list-type="picture">
                <el-button size="small" type="primary"></el-button>
                <div slot="tip" class="el-upload__tip">jpg/png</div>
              </el-upload>
            </div>

            <div class="cencen">            
              <el-input v-model="pictureNode.title" placeholder="~"></el-input>
              
              <div class="cencen"> 
                 <el-input
                   type="textarea"
                   :autosize="{ minRows: 2, maxRows: 4}"
                   placeholder=""
                   v-model="pictureNode.content">
                 </el-input>
              </div>
            </div>

            <div class="cencen">
              <!-- //        -->
              <el-radio v-model="pictureNode.tag" label="" border></el-radio>
              <el-radio v-model="pictureNode.tag" label="" border></el-radio>
              <el-radio v-model="pictureNode.tag" label="" border></el-radio>
              <el-radio v-model="pictureNode.tag" label="" border></el-radio>
              <el-radio v-model="pictureNode.tag" label="" border></el-radio>
              <el-radio v-model="pictureNode.tag" label="" border></el-radio>
            </div>

            <div class="pic_publish">
               <img :src="require('../../assets/mypic/pubNote.png')" alt="" srcset="">
               <div class="pub_pic_btn">
                <el-button type="primary" @click="publish_PictureNode"></el-button>
                <el-button plain></el-button>
               </div>
            </div>
        </div>
         
      </div>


      <!--  -->
      <div class="note" v-if="pic_or_vlog == 'pub_vlog' ">
        <div class="note_real">
            <div class="cencen">
              <span></span>
              <el-button type="text"> &nbsp; +</el-button> &nbsp;&nbsp; <em style="font-size: small; font-weight: 100;"></em>
              <br>
              <el-upload class="avatar-uploader" action="" :http-request="upload_VideoNode_surfacePicture" :show-file-list="false"
                  :on-success="handleAvatarSuccess_videoNode" :before-upload="beforeAvatarUpload_videoNode">
                  <img v-if="videoNote.surfacePicture" :src="videoNote.surfacePicture" class="avatar">
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </div>
            <div class="cencen">
              <el-upload
                class="upload-demo"
                drag
                action=""
                :file-list="videoFile"
                :http-request="upload_VideoNode_videurl"
                multiple>
                <i class="el-icon-upload"></i>
                <div class="el-upload__text"><em></em></div>
                <!-- <div class="el-upload__tip" slot="tip">jpg/png500kb</div> -->
              </el-upload>
            </div>

            <div class="cencen">            
              <el-input v-model="videoNote.title" placeholder="~"></el-input>
              
              <div class="cencen"> 
                 <el-input
                   type="textarea"
                   :autosize="{ minRows: 2, maxRows: 4}"
                   placeholder=""
                   v-model="videoNote.content">
                 </el-input>
              </div>
            </div>

            <div class="cencen">
              <!-- //        -->
              <el-radio v-model="videoNote.tag" label="" border></el-radio>
              <el-radio v-model="videoNote.tag" label="" border></el-radio>
              <el-radio v-model="videoNote.tag" label="" border></el-radio>
              <el-radio v-model="videoNote.tag" label="" border></el-radio>
              <el-radio v-model="videoNote.tag" label="" border></el-radio>
              <el-radio v-model="videoNote.tag" label="" border></el-radio>
            </div>

            <div class="pic_publish">
               <img :src="require('../../assets/mypic/pubNote.png')" alt="" srcset="">
               <div class="pub_pic_btn">
                <el-button type="primary" @click="publish_videoNode"></el-button>
                <el-button plain></el-button>
               </div>
            </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        // 
        videoFile: [
          // {name: 'food.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}
        ],
        fileList: [
          {name: 'food.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}
        ],
        pic_or_vlog: 'pub_picture', // 
        //  
        dialogPictureNode: {
          dialogImageUrl: '',
          dialogVisible: false
        },
        // 
        pictureNode : {
          // 
          surfacePicture: "",
          // 
          title: "",
          // 
          content: "",
          // 
          tag: "",
          // 
          pictureurl: [],
        },
        // 
        videoNote : {
          // 
          surfacePicture: "",
          // 
          title: "",
          // 
          content: "",
          // 
          tag: "",
          // 
          videourl: "",
        },
      };
    },
    methods: {
      // 
      handleSelect(key, keyPath) {
        // console.log(key);
        this.pic_or_vlog = key
      },
      // ======================================================================

      //----------------------------------------------------------------------
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        // this.dialogPictureNode.dialogImageUrl = file.url
        // this.dialogPictureNode.dialogVisible = true;
        console.log(file)
      },
      //  http://localhost:8080/release/uploadPicture 
      //  http://localhost:8080/user/headphoto
      upload_PictureNode_pictureurl(param) {
            const formdate = new FormData()
            formdate.append('headphoto', param.file)
            const url = '/user/headphoto'
            const headers = {
                'Content-Type': 'multipart/form-data'
            }
            // axios.post
            this.$http.post(
                url,
                formdate,
                headers
            ).then(res => {
                // console.log(data)
                this.$message.success("")
                // 
                // fileListurlpictureNodepictureurl
                this.fileList.push({name: param.file.name, url: res.data.data})
                
            }).catch(response => {
                console.log(response)
            })
        },

        // ----------------------------------------------------------------------
        // 
        upload_PictureNode_surfacePicture (param) {
          const formdate = new FormData()
            formdate.append('headphoto', param.file)
            const url = '/user/headphoto'
            const headers = {
                'Content-Type': 'multipart/form-data'
            }
            // axios.post
            this.$http.post(
                url,
                formdate,
                headers
            ).then(res => {
                // console.log(data)
                this.$message.success("")
                this.pictureNode.surfacePicture = res.data.data
                
            }).catch(response => {
                console.log(response)
            })
        },
        handleAvatarSuccess_pictureNode(res, file) {
            this.pictureNode.surfacePicture = URL.createObjectURL(file.raw);
        },
        beforeAvatarUpload_pictureNode(file) {},

        // ----------------------------------------------------------------------
        //  http://localhost:8080/release/note
        publish_PictureNode () {
          for (var i = 0; i < this.fileList.length; i++) {
            this.pictureNode.pictureurl.push(this.fileList[i].url)
          }
          
          this.$http({
            url: '/release/note',
            data: this.pictureNode,
            method: 'post'
          }).then(res => {
              if ("" == res.data.data) {
                 this.$message.success("")
                 // 
                 this.pictureNode =  {
                    surfacePicture: "",
                    title: "",
                    content: "",
                    tag: "",
                    pictureurl: [],
                 }
              }
          }).catch(res => {
            console.log(res);
            this.$message.error("")
          })
        },



      // ======================================================================

      // ------------------------------------------------------------------
      // 
      upload_VideoNode_surfacePicture (param) {
          const formdate = new FormData()
            formdate.append('headphoto', param.file)
            const url = '/user/headphoto'
            const headers = {
                'Content-Type': 'multipart/form-data'
            }
            // axios.post
            this.$http.post(
                url,
                formdate,
                headers
            ).then(res => {
                // console.log(data)
                this.$message.success("")
                console.log(this.videoNote)
                this.videoNote.surfacePicture = res.data.data
            }).catch(response => {
                console.log(response)
            })
        },
        handleAvatarSuccess_videoNode(res, file) {
            this.videoNote.surfacePicture = URL.createObjectURL(file.raw);
        },
        beforeAvatarUpload_videoNode(file) {},
        
        // ----------------------------------------------------------------------
        //  http://localhost:8080/release/uploadVideo  name: param.file.name
        upload_VideoNode_videurl (param) {
          const formdate = new FormData()
            formdate.append('video', param.file)
            const url = '/release/uploadVideo'
            const headers = {
                'Content-Type': 'multipart/form-data'
            }
            // axios.post
            this.$http.post(
                url,
                formdate,
                headers
            ).then(res => {
                // console.log(data)
                this.$message.success("")
                this.videoFile.push({name: param.file.name, url: res.data.data})
                this.videoNote.videourl = res.data.data
                // console.log(this.videoNote)
            }).catch(response => {
                console.log(response)
            })
        },
        // ----------------------------------------------------------------------
        //  http://localhost:8080/release/note
        publish_videoNode () {
          for (var i = 0; i < this.fileList.length; i++) {
            this.pictureNode.pictureurl.push(this.fileList[i].url)
          }
          
          this.$http({
            url: '/release/note',
            data: this.videoNote,
            method: 'post'
          }).then(res => {
              if ("" == res.data.data) {
                 this.$message.success("")
                 // 
                 this.videoNote =  {
                    surfacePicture: "",
                    title: "",
                    content: "",
                    tag: "",
                    videourl: [],
                 }
              }
          }).catch(res => {
            console.log(res);
            this.$message.error("")
          })
        }
    }
  }
</script>

<style lang="less" scoped>
.pubnote {
  width: 60%;
  height: 100%;
  background-color: white;
  border-radius: 20px;
  // overflow: hidden;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  overflow: scroll;
  .el-menu--horizontal.el-menu {
    padding-left: 8px;
    padding-right: 8px;
  }
}
.pubnote::-webkit-scrollbar {
    width: 0;
    height: 0;
    background-color: transparent;
  }

.note {
  position: relative;
  width: 100%;
  height: 95%;
  .note_real {
    width: 96%;
    height: 96%;
    position: absolute;
    top:50%;
    left: 50%;
    transform: translate(-50%,-50%);
    // background-color: aquamarine;
  }
}

.cencen {
  margin-top: 10px;
}

.pic_publish {
  position: relative;
  margin-top: 24px;
  background-color: rgb(238, 241, 248);
  width: 30%;
  height: 65px;
  line-height: 65px;
  border-radius: 10px;
  img {
    height: 65px;
    width: auto;
  }
  .pub_pic_btn {
    position: absolute;
    top: 50%;
    right: 20px;
    transform: translateY(-50%);
  }
}
</style>